




























import Vue from "vue";
import { GlobalService, Component } from "../../service";
export default Vue.extend({
  data() {
    return {
      visible: false,
      form: {
        path: "",
        js: "",
        css: ""
      } as Component
    };
  },
  methods: {
    open(src: Component) {
      if (src) {
        this.form = src;
      }
      this.visible = true;
    },
    async onSubmit() {
      try {
        await GlobalService.createComonent(this.form);
        this.$emit("submit");
      } catch ({ response }) {
        if (response.status === 422) {
          this.$message.error(response.data.message);
        }
      }
    }
  }
});
